<template>
	<div ref="wrapper">
		<slot></slot>
	</div>
</template>
<script>
	import BScroll from 'better-scroll'

	export default {
		name:'scroll',
		props: {
			probeType: {
				type: Number,
				default: 1
			},
			click: {
				type: Boolean,
				default: true
			},
			listenScroll: {
		        type: Boolean,
		        default: true
		     },
			data: {
				type: Array,
				default: null
			}
		},
		mounted() {
			setTimeout(() => {
				this._initScroll()
			},20)
		},
		methods: {
			_initScroll() {
				if (!this.$refs.wrapper) {
					return
				}
				this.scroll = new BScroll(this.$refs.wrapper,{
					probeType:this.probeType,
					click: this.click
				})
				if (this.listenScroll) {
		          let me = this
		          this.scroll.on('scroll', (pos) => {
		            me.$emit('scroll', pos)
		          })
		        }
			},
			enable() {
				this.scroll && this.scroll.enable()
			},
			disable() {
				this.scroll && this.scroll.disable()
			},
			refresh() {
				this.scroll && this.scroll.refresh()
			}
		},
		watch: {
			data() {
				setTimeout(() => {
					this.refresh()
				},20)
			}
		}
	}
</script>
<style scoped lang='stylus'>
	
</style>


